<template>
	<view>
		<!-- #ifdef MP-WEIXIN -->
		<!-- 小程序端搜索框 -->
		<i-search-input></i-search-input>
		<!-- #endif -->
	</view>
</template>

<script>
	// 引入搜索框组件
	import searchInput from "@/components/common/search-input.vue"
	export default {
		data() {
			return {
				
			}
		},
		components : {
			"i-search-input" : searchInput
		},
		onLoad() {
			// #ifdef APP-PLUS
			// 这个方法只在APP端有效
			this.handleUpdatePlaceholderText()
			// #endif
			
		},
		methods: {
			// 动态切换APP端搜索提示信息
			handleUpdatePlaceholderText(){
				// 1. 获取到页面实例
				const webview = this.$scope.$getAppWebview();
				
				// 2. 定义导航栏搜索框的数据源
				const arr = [ 'APP · 微信小程序', 'Java · SprinBoot', 'SpringCloud · SpringSecurity', 'Vue · React']
				let i = 0;
				
				// 3. 调用设置导航栏的方法
				this.handleSetPlaceholderText(arr[i])
				
				// 4. 动态切换搜索框提示文本
				setInterval(()=>{
					i = i < 3 ? ++i : 0
					this.handleSetPlaceholderText(arr[i])
				},3000)
				
				
			},
			// 动态设置搜索框提示文本
			handleSetPlaceholderText(data){
				webview.setStyle({
					"titleNView" : {
						"searchInput" : {
							"placeholder" : data
						}
					}
				})
			}
		}
	}
</script>

